/* General Styles for About Us Section */
.about-us-section {
  padding: 60px 20px; /* Adjust padding for spacing */
  display: flex;
  justify-content: center;
}

/* Content Layout */
.about-us-content {
  display: flex;
  align-items: center;
  max-width: 800px;
  gap: 50px;
}

/* Image Styling */
.about-us-image {
  width: 50%;
  max-width: 400px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

/* Text Styling */
.about-us-text {
  width: 50%;
  font-size: 1.1em;
  color: #555;
}

.about-us-text h2 {
  margin: 0 0 10px;
  font-size: 2em;
  color: #333;
}

.about-us-text p {
  margin: 0;
}

/* Hover Effect */
.about-us-image:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease;
}

/* Responsive for Medium Screens (Tablets, 768px and below) */
@media (max-width: 768px) {
  .about-us-content {
    flex-direction: row; /* Keep horizontal layout for medium screens */
    gap: 20px; /* Reduce gap between items */
  }

  .about-us-text {
    font-size: 1em; /* Reduce font size for medium screens */
  }

  .about-us-text h2 {
    font-size: 1.5em; /* Reduce heading size */
  }

  .about-us-text p {
    line-height: 1.4; /* Improve readability with reduced font size */
  }

  .about-us-section {
    padding: 40px 15px; /* Reduce padding */
  }
}

/* Responsive for Small Screens (Mobile Phones, 480px and below) */
@media (max-width: 480px) {
  .about-us-content {
    flex-direction: row; /* Maintain horizontal layout for small screens */
    align-items: flex-start; /* Align items properly for small space */
    gap: 15px; /* Reduce gap further for small screens */
  }

  .about-us-image {
    width: 40%; /* Adjust image size for smaller screens */
    max-width: 300px; /* Limit maximum width */
  }

  .about-us-text {
    width: 60%; /* Ensure the text fits next to the image */
    font-size: 0.9em; /* Smaller font size for small screens */
  }

  .about-us-text h2 {
    font-size: 1.2em; /* Reduce heading size */
  }

  .about-us-text p {
    font-size: 0.8em; /* Adjust paragraph font size */
    line-height: 1.3; /* Compact line spacing */
  }

  .about-us-section {
    padding: 30px 10px; /* Minimal padding for mobile devices */
  }
}
