.services-section {
  text-align: center;
  padding: 20px;
  background-color: #f9f9f9;
}

.services-section h1 {
  font-size: 2.5em;
  color: #333;
  margin-bottom: 40px;
}

.specialties-list {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
  overflow-x: auto; /* Enable horizontal scrolling on small/medium screens */
  padding: 20px;
  scroll-behavior: smooth; /* Smooth scrolling */
}

.specialty-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0 0 auto; /* Prevent shrinking */
  width: 180px; /* Fixed width */
  transition: transform 0.3s ease-in-out; /* Smooth transition for hover */
}

.specialty-item:hover {
  transform: translateY(-5px) scale(1.1); /* Hover effect: slight lift and zoom */
}

.specialty-image {
  border-radius: 50%;
  object-fit: cover;
  width: 150px;
  height: 150px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #f9f9f9;
}

.specialty-name {
  margin-top: 10px;
  font-size: 1.2em;
  color: #555;
}




/* Adjustments for medium screens */
@media (max-width: 1024px) {
  .specialty-item {
    width: 200px; /* Increase item width on medium screens */
  }
}

/* Adjustments for small screens */
@media (max-width: 600px) {
  .specialties-list {
    gap: 20px; /* Reduce gap on small screens */
  }

  .specialty-item {
    width: 150px; /* Adjust item width on small screens */
  }
}
