/* Base Styles */
.registration-container {
  display: flex;
  align-items: center;
  padding: 10px;
  gap: 140px;
  flex-wrap: wrap; /* Allows wrapping on smaller screens */
}

.registration-image {
  flex: 1;
  min-width: 300px;
  max-width: 700px;
  height: auto;
}

.registration-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.registration-form-container {
  flex: 1;
  min-width: 300px;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  text-align: center;
  margin-bottom: 30px;
  font-size: 2.5rem;
  color: #333;
}

.registration-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
}

label {
  font-size: 16px;
  margin-bottom: 5px;
  color: #333;
}

input {
  padding: 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
}

.submit-btn {
  background-color: #FF5722;
  color: white;
  border: none;
  padding: 15px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  width: 100%;
}

.submit-btn:hover {
  background-color: #e64a19;
}

/* Responsive Styles */

/* For Big Screens (1200px and above) */
@media screen and (min-width: 1200px) {
  .registration-container {
    padding: 60px;
    gap: 160px;
  }

  h1 {
    font-size: 3rem;
  }

  .registration-form-container {
    padding: 0 30px;
  }
}

/* For Medium Screens (Tablets, 768px to 1199px) */
@media screen and (max-width: 1199px) {
  .registration-container {
    padding: 20px;
    gap: 80px;
    flex-direction: row; /* Keeps form and image side-by-side for tablets */
  }

  h1 {
    font-size: 2.2rem;
  }

  input {
    padding: 10px;
    font-size: 14px;
  }

  .submit-btn {
    padding: 14px;
    font-size: 15px;
  }
}

/* For Small Screens (Mobile, 767px and below) */
@media screen and (max-width: 767px) {
  .registration-container {
    flex-direction: column; /* Stacks form and image vertically */
    gap: 20px;
    padding: 15px;
  }

  h1 {
    font-size: 1.8rem;
  }

  label {
    font-size: 14px;
  }

  input {
    padding: 10px;
    font-size: 13px;
  }

  .registration-form-container {
    width: 100%;
    padding: 0 10px;
  }

  .submit-btn {
    padding: 12px;
    font-size: 14px;
  }
}
